    export default {
        data() {
            return {
                msgContents: ["界面显示错乱", "启动缓慢，卡出翔了", "UI无法直视，丑哭了", "偶发性崩溃"],
                stars: [1, 2, 3, 4, 5],
                imageList: [],
                sendDate: {
                    score: 0,
                    content: "",
                    contact: ""
                },
				upImgPath:null
            }
        },
        onLoad() {
        },
        methods: {
            close(e){
                this.imageList.splice(e,1);
            },
            chooseMsg() { //快速输入
                uni.showActionSheet({
                    itemList: this.msgContents,
                    success: (res) => {
                        this.sendDate.content = this.msgContents[res.tapIndex];
                    }
                })
            },
            chooseImg() { //选择图片
                uni.chooseImage({
                    sourceType: ["camera", "album"],
                    sizeType: "compressed",
                    count: 1,
                    success: (res) => {
                        this.imageList = this.imageList.concat(res.tempFilePaths);
						this.userUploadFile(this.imageList[0])
                    }
                })
            },
            chooseStar(e) { //点击评星
                this.sendDate.score = e;
            },
            previewImage() { //预览图片
                uni.previewImage({
                    urls: this.imageList
                });
            },
			userUploadFile(picPath) {
				var that = this;
				uni.uploadFile({
					url: that.global_baseUrl + '/index.php/upPic',
					filePath: picPath,
					name: 'file',
					header: {
						'content-type': 'application/json'
					},
					success: (uploadFileRes) => {
						const tempPath = JSON.parse(uploadFileRes.data).data.src;
						that.upImgPath = tempPath.replace(/\\/g, "/");
					}
				});
			
			},
            send() { //发送反馈
			var that = this;
			var tempData = {
				content:that.sendDate.content,
				image: that.upImgPath,
				email:that.sendDate.contact,
				grade:that.sendDate.score
			}
			uni.request({
				url: that.global_baseUrl + '/index.php/problem',
				data: tempData,
				method: "POST",
				success: (res) => {
					if (res.data.code == '200') {
						var text = '感谢您的反馈！我们将会变得更好，为您提供更好的体验！'
						uni.navigateTo({
							url: '/pages/public/feedbackPage/feedbackPage?content=' + text
						})
					} else {
			
					}
				}
			});
            }
        }
    }
